.login{
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background:
     linear-gradient(
        rgba(255, 255, 255, 0.5),
        rgba(255, 255, 255, 0.5)
      ),
      url("https://images.pexels.com/photos/768473/pexels-photo-768473.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500");
    background-size: cover;
}   

.loginTitle{
    font-size: 50px;
}

.loginForm{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    
}

.loginForm>label{
    margin: 10px 0;
    
}

.loginInput{
    background-color: white;
    border: none;
}


.loginButton{
    margin-top: 20px;
    cursor: pointer;
    background-color:lightcoral;
    border: none;
    color: white;
    border-radius: 10px;
    padding: 10px;
}

.loginRegisterButton{
    position: absolute;
    top: 60px; ;
    right: 20px;
    cursor: pointer;
    background-color:teal;
    border: none;
    color: white;
    border-radius: 10px;
    padding: 10px;

}